<template>
  <div class="scene" v-if="sceneList && sceneList.length">
    <div class="bg-tag"/>
    <div class="sence-title">
      <span class="text">推荐智慧场景</span>
      <a href="javascript:;" @click="goSyn(false)" v-if="storeSceneData && storeSceneData.storeSenceUrl">更多 <i></i></a>
    </div>
    <div class="sence-item-warp">
      <template v-if="sceneList && sceneList.length > 0">
        <div class="program-product-left">
          <div class="program-product-item" v-for="(scene,index) in sceneList.filter((v, i) => !(i % 2))" :key="scene.name" @click="goSyn(scene,index+1)">
            <div class="program-product-img">
              <div class="program-play" v-if="scene.videoUrl"><span></span></div>
              <vanImg :src="scene.thumbnailUrl | imageToHttps" />
            </div>
            <p>{{scene.name}}</p>
          </div>
        </div>
        <div class="program-product-right">
          <div class="program-product-item" v-for="(scene,index) in sceneList.filter((v, i) => i % 2)" :key="scene.name" @click="goSyn(scene,index+2)">
            <div class="program-product-img">
              <div class="program-play" v-if="scene.videoUrl"><span></span></div>
              <vanImg :src="scene.thumbnailUrl | imageToHttps" />
            </div>
            <p>{{scene.name}}</p>
          </div>
        </div>
      </template>
      <common-empty v-else/>
    </div>
  </div>
</template>
<script>
const commonEmpty = () => import('../../commonEmpty');

/** 精选--优选场景 */
export default {
  name: 'scene',
  props: {
    storeSceneData: {
      type: Object,
      default: () => {},
    },
    data: {
      type: Object,
      default: () => {},
    },
    storeId: {
      type: String,
      default: '',
    },
  },
  components: {
    commonEmpty
  },
  methods: {
    goSyn(scene, index) {
      let jumpUrl = this.storeSceneData.storeSenceUrl

      if (scene) {
        // 25093埋点
        this.$util.gioTrackWithVariable('mall_store_click_model', {
          modelName: '优选场景',
          modelCode: 'scenes',
          site_number: index + '',
          itemCode: 'null',
        })
        jumpUrl = scene[this.storeSceneData.sceneUrlKey]
        if (!jumpUrl.includes('?')) {
          jumpUrl += '?'
        } else {
          jumpUrl += '&'
        }
        jumpUrl += `userId=${this.$store.state.ucId ? this.$store.state.ucId : ''}&userAccessToken=${this.$store.getters.getUserCenterToken}`
      } else {
        // 25093埋点
        this.$util.gioTrackWithVariable('mall_store_click_model', {
          modelName: '优选场景',
          modelCode: 'scenes',
          site_number: 'null',
          itemCode: 'null',
        })
      }

      this.$store.dispatch('goPageByType', {
        type: this.storeSceneData.jumpType,
        params: this.$util.injectISOAddressCodeToURL(jumpUrl, this.$store.getters.getlocationInfo)
      })
    },
  },
  computed: {
    sceneList() {
      return this.storeSceneData?.sences || []
    }
  }
}
</script>

